.section-container{
  margin: auto;
  max-width: 80vw;

  @media all and (max-width:1024px) {
    max-width: 90vw;
  }
}

@media all and (max-width: 850px) {
  .contact--form--section .d-inline {
    display: initial;
  }

  .contact--form--section form {
    width: 50vw;
  }

  .contact--form--section .i--style input {
    width: calc(100% - 5rem);
  }

  .portfolio--container .mySwiper {
    width: 100vw;
  }

