$input--color: #f3f3f3;
// $main--color: #6a72fc;
$main--color: #6256bf;

@mixin flex($just, $align, $dir: row) {
  display: flex;
  flex-direction: $dir;

  @if ($dir == column) {
    justify-content: $align;
    align-items: $just;
  } @else {
    justify-content: $just;
    align-items: $align;
  }
}

