.contact--section {
  padding: 4rem 0rem;
  // background-color: yellow;
}
.contact--container {
  padding: 4rem 0rem;
  display: flex;

  justify-content: center;

  @media all and (max-width: 1500px) {
    justify-content: space-evenly;
  }
}

.contact--section > .title {
  text-align: center;
  // background-color: red;

  label {
    font-size: 3rem;
    font-weight: 500;
  }

  span {
    display: block;
    color: rgba($color: #000000, $alpha: 0.7);
  }
}

// contact card component
.contact--sideCard {
  margin: 1rem;
  display: flex;
  label {
    display: block;
  }
}

.contact--icon {
  font-size: 2.5rem;
  color: $main--color;
}

.contact--details,
.contact--icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.3rem;
}

.contact--details label:nth-child(1) {
  font-size: 1.2rem;
  font-weight: 500;
}

.contact--details label:nth-child(2) {
  color: rgba($color: #000000, $alpha: 0.5);
}

// 1st 2 fields of form
.d-inline {
  @include flex(initial, center);
  // width: 100%;
  // background-color: red;
}

textarea {
  height: 10rem;
}

.i--style input,
textarea {
  background-color: $input--color;

  margin: 1rem;
  padding: 1rem 1.5rem;

  outline: none;
  border: none;
  border-radius: 0.5rem;

  font-size: 1rem;
  // width: calc(100% - 2rem);
}

[type="tel"],
textarea {
  width: calc(100% - 5rem);
}

.submit--btn {
  background-color: $main--color;
  color: white;

  display: flex;
  align-items: center;

  width: fit-content;
  margin: 1rem;
  padding: 1rem;

  font-size: 1.2rem;
  border-radius: 0.3rem;

  border: none;
  outline: none;

  transition: all 0.2s;

  i {
    margin-left: 0.3rem;
  }
}

.submit--btn:hover,
.submit--btn > * {
  cursor: pointer;
}

.submit--btn:hover {
  transform: translateY(2px);
}


@media all and (max-width:850px) {
    .contact--form--section{
        width: 100%;
    }
}

    @media all and (max-width:600px) {
        
        .contact--container{
            justify-content: initial;
            flex-wrap: wrap;
            gap: 3rem;
        }
    }